
<template>
     <div>
     <loader :is-visible="isLoading"></loader>
        <TgList IsShowAction="true"
        ModuleName="CRM" 
        SubModuleCode="CRM_DEALS" 
        IdentityColumn="deal_id"
        :HeaderText="$t('Deal')"
        :ListData="DealData" 
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" 
        :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields"
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"
        :LegendArray="LegendArray"
        :IsFilterApplied="searchCondition?true:false"
        :SortExp="SortExp"
        :SortBy="SortBy"
        :CurrentPageNumber="PageNumber"
        v-on:LeftsearchButtonClick="leftCommonSearch"
        v-on:PagerButtonClick="pagerMethod" 
        v-on:HeaderButtonClick="commonHeaderButtonClick"
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status  :isfor="'CRM_DEALS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_DEALS')" ref="updatestatus" v-bind:dataid="data.row.deal_id" 
                data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index"  v-bind:dataselected="data.row.status_id" 
                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_DEALS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>
        </template>
    </TgList>
    <modalquickviewdetails v-if="showQuickLeadViewModal" @close="closeQuickViewModal()"
        :sourceIds="idForViewRecord" modulename="CRM" submodulecode="CRM_DEALS" controller="crm"
        actions="ViewDealDetails">
    </modalquickviewdetails>
    <modalnote v-if="showNoteModal" @close="showNoteModal = false" :DealInfo="DealIdForViewNote"
       modulename="CRM" submodulecode="CRM_DEALS" controller="crm"
        actions="ViewDealDetails" :userid="userid" v-bind:curindex="CurIndex">
    </modalnote>
    <modalfollowupslist :srcId="souceID" :DealInfo="selectedItem" v-if="showAddEventModal"
                    modulename="CRM" submodulecode="CRM_DEALS" :userid="userid"
                    v-on:close="closeEventViewModal">
    </modalfollowupslist>
    <searchfilter 
                v-if="showFilter" 
                submodulecode="CRM_DEALS" 
                modulename="CRM" 
                v-on:close="showFilter = false" 
                v-bind:callbackfunction="FetchData"
                v-on:FilterData="FilterData">
                <h5 slot="header">{{$t('Search')}}</h5>
            </searchfilter>    
    <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_DEALS"  modulename="CRM"   v-on:close="ShowManageView = false" v-bind:callbackfunction="FetchData"  :usertype="usertype">
        <h5 slot="header">{{$t('Search')}}</h5>
    </viewlayout>
    <modalleadowner v-if="showDealOwnerModal" :OwnerIds="OwnerIds" :ModuleType="ModuleType"  v-on:close="onCloseDealOwner">
        <h5 slot="header">{{$t('DealOwner')}}</h5>
    </modalleadowner>
    <addadditionalowner v-if="showAdditionalOwnerPopup" subModuleCode="CRM_DEALS" @close="showAdditionalOwnerPopup = false,onChange()" ></addadditionalowner>
</div>   
</template>
<script>
import modalquickviewdetails from '../../../components/Common/Modal/modalquickviewdetails.vue'
import modalnote from '../../../components/Common/Modal/modalnote.vue'
import modalfollowupslist from '../../../components/Common/Modal/modalfollowupslist.vue'
import DealDataService from "../../../services/DataService"
import Schema from "../../CRM/Deal/DealListingSchema"
import searchfilter from '../../../components/Common/Modal/search-filter.vue'
import modalleadowner from '../../../components/Common/Modal/modalleadowner.vue'
import addadditionalowner from '../../../components/Common/Modal/addadditionalowner.vue'
import  viewlayout from '../../../components/Common/Modal/view-layout.vue'
import TgList from '../../../components/Common/TgList.vue'
export default {
    components: {
        viewlayout,
        addadditionalowner,
        modalleadowner,
        TgList,
        modalfollowupslist,
        modalquickviewdetails,
        modalnote,
        searchfilter
    },
    data() {
   return {
            ShowManageView: false,
            showDealOwnerModal: false,
            isLoading: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            DealStatusList: null,
            DealData: [],
            clientemail: '',
            NameSearch: '',
            CompanyName: '',
            hdnOwnerId: '',
            Headers: [],
            souceID: 0,
            showAddEventModal: false,
            //showLeadOwnerModal: false,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            Status: '',
            OwnerId: '',
            OwnerIds: [],
            ModuleType:'',
            SearchClient: '',
            leadDateFrom: '',
            leadDateTo: '',
            SearchCompay: '',
            Channel: '',
            showImportModal: false,
            showConvertLead: false,
            showAddEmailTemplate: false,
            convertLeadValue: {
                value: null,
                name: ''
            },
            modalItem: [],
            showExportModal: false,
            showModal: false,
           noRecord: false,
            showFilter: false,
            searchCondition: '',
            showQuickLeadViewModal: false,
            showNoteModal: false,
            isShowLeadNoteDialogue: false,
            idForViewRecord: null,
            NoRecordForTabNotes: false,
            LeadIdForViewNote: null,
            LeadOwnerList: null,
            LeadStatusList: null,
            selectedLeadOwner: [],
            IsAnySearch: false,
            CurIndex: 0,
            conditionForInstantSearch: "",
            showAdditionalOwnerPopup: false,
            listActions: Schema.listActions,
            StatusListForListing: [],
            userid: null,
            companyId: null,
          
           LegendArray: [],
            usertype:null,
            LoggedinUserId: null,
            privilegeParams: null,
            appliedFilter:[]
        }
    },
    created: function () {    
       
          this.userid = this.GetUserInfo.ID
            this.usertype = this.GetUserInfo.UserType,
            this.companyId = this.GetUserInfo.CompanyId;
            
        this.privilegeParams = `controller=CRM&action=`;  
        var ths = this;        
       
        Schema.bindleftCommonSearchdropdown(ths, "CRM", "DEAL_OWNER_SEARCH"); //ths.bindleftCommonSearchdropdown("CRM","DEAL_OWNER_SEARCH");//ths.GetLeadOwnerlist();
        Schema.bindleftCommonSearchdropdown(ths, "CRM", "LEAD_DEAL_STATUS"); //ths.bindleftCommonSearchdropdown("CRM","LEAD_DEAL_STATUS");//ths.GetSearchStatusList();
        Schema.bindleftCommonSearchdropdown(ths, "CRM", "CRM_STAGE"); //ths.bindleftCommonSearchdropdown("CRM","CRM_STAGE");//ths.GetDealStageData();
        ths.FetchData();
        ths.LegendArray = [
                {
                    class: 'legend-pending colorboxsquare',
                    text: (ths.$t('CloseLeadLegend', ['Lead']))
                }
            ]
    },
   
    methods: {
        renderActions: function(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {...actionItem};
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = ((self.isBlank(row.existing_client_id) || self.isBlank(row.project_id)) && (row.status_id != 'Closed' && row.status_id != 'Cancelled'))
                        break;
                    case 'FollowUps':
                    action.isVisible = row.status_id != 'Closed';
                        break;
                    case 'Revert to Lead':  
                  action.isVisible = row.status_id =='Active' && row.Lead_ID!=0 && row.Quot_id ==0;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                    
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        clickEventOfColumn:function(row, column){
            if(column.COLUMN_NAME == 'deal_name'){
                this.$router.push({ path: '/CRM/ViewDealDetails/'+row.deal_id+'/CRM/CRM_DEALS' });
            }
        },
        columnDataFormatter: function(row, column){
            return row[column.COLUMN_NAME];
        },
        commonHeaderButtonClick: function (event) {
           
            switch (event.callbackfunction) {
                case "downloadExceldata":
                    this.FetchData(true);
                    break;
                case "sendBulkEmail":
                    break;
                case "ManageView":
                    this.ManageView();
                    break;

                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "ChangeDealOwner":
                    this.ChangeDealOwner();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break; 
                case "checkPrivilegeDetailsForOwner":
                    this.checkPrivilegeDetailsForOwner(event, null);
                     break; 
                case "removeFilter": 
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';});
                    ths.listheaderbuttons[removeFilterindex].isvisible=false;
                    const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';});
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                     try {
                        ths.appliedFilter = [];
                     } catch (e) { };
                     ths.FetchData();
                    break;                   
                     case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                        break; 

            }
        },
        RevertToLead: function (item) {
            var vm = this;
           
            var objectPostString = {
                ID: item.deal_id,
                IsFor: 'CRM_DEALS'
            }
            vm.confirmR("Are you sure, You want to revert Deal?", true, "", function (){
                 
                DealDataService.RevertToLead(objectPostString)
                        .then(function (response) {
                            var json = response.data;
                             
                            if (json[0].Status == "Success") {
                                vm.ShowAlert(vm.$t("Deal has been Reverted successfully"),"success", true,vm.$t('Alert'));
                                vm.FetchData();                    
                            }
                             
                        });
            });
        },
        commonDeleteData: function (event) {
               
                    var vm = this;
                    DealDataService.CheckPrivilege(vm.privilegeParams+'DeleteDeal')
                        .then(function (response) {          
                            if (response.data) {
                                var val = "";
                                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                    if (val.length > 0)
                                        val += ',';
                                    val += $(this).val();
                                });
                                if (val.length > 0) {
                                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Deal")), function () {
                                        var postJSON = {
                                            ids: val,
                                            moduleName: "CRM",
                                            subModuleCode: "CRM_DEALS"
                                        };
                                        var objectPostString = JSON.stringify(postJSON);
                                        DealDataService.deleteActivityData(objectPostString).then(function (response) {

                                            var box = "";
                                            if (response.data[0].Status == "Success") {
                                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                                $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                                                $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');

                                                $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                                                $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');

                                                vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                                if (vm.PageNumber <= 0) {
                                                    vm.PageNumber = 1;
                                                }
                                                vm.FetchData();

                                                box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Deal')]), "success", true, vm.$t("Alert"));
                                            }
                                            else {
                                                box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Deal')]), "success", true, vm.$t("Alert"));
                                            }
                                            setTimeout(function () {
                                                box.modal('hide');
                                            }, 2000);

                                        },);
                                    });
                                }
                                else {
                                    vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, "Alert");
                                }
                            }
                            else {
                                vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                            }
            });
            },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },        
        ManageView: function () {
                var vm = this;
                vm.ShowManageView = true;   
                setTimeout(function () {
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        vm.SetBootstraptoolTip();
                    }, 200);
                }, 1000);
          
        },       
        

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        
            getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
                let params = {};
                if (pageNum) { params["pageNum"] = pageNum; }
                if (searchcondition) { params["searchcondition"] = searchcondition; }
                if (isPaged) { params["isPaged"] = isPaged; }
                if (sortBy) { params["sortBy"] = sortBy; }
                if (sortExp) { params["sortExp"] = sortExp; }
                if (pageSize) { params["pageSize"] = pageSize; }
                return params;
            },
            FilterData:function(pageNum,pageSize,sortBy,sortExp,searchcondition,filterItemData){
               this.pageNum=pageNum;
               this.pageSize=pageSize;
               this.sortBy=sortBy;
               this.sortExp=sortExp;
               this.searchCondition=searchcondition;
               if(typeof filterItemData!=null)
               this.appliedFilter.push(filterItemData);
             this.FetchData();
        },
        FetchData: function (downloadExceldata) {
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
                 var vueObj = this;
                 this.isLoading = true;
                 if(downloadExceldata){
                    params.pageSize = vueObj.TotalRecords;
                 }else{
                    params.pageSize = vueObj.PageSize;
                 }
                params.PageNumber = vueObj.PageNumber;
                params.searchCondition = vueObj.searchCondition;
                if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
                else { vueObj.PageNumber = params.pageNum = 1; }

                if (vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
                else { vueObj.isPaged = params.isPaged = 1; }
                params.isPaged = vueObj.isPaged;
                params.isPaged = vueObj.isPaged;
                params.sortBy = vueObj.SortBy;
                params.sortExp = vueObj.SortExp;
                var srchCondition = "";
                if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
                if (!this.isBlank(this.conditionForInstantSearch)) {
                    srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
                }
                if (srchCondition != "") {
                    params.searchCondition = srchCondition; 
                    if(vueObj.appliedFilter.length>0){
                    const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';
                    });          
                    vueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                    const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
                }               
            }
            else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
          }
            if (downloadExceldata) {
                console.log(params);
                params.isPaged = 0;
                vueObj.isLoading = true;
                DealDataService.DealListing(params)
                    .then(function (response) {
                        if (response.data != "" && response.data != null) {
                            vueObj.JSONToCSVConvertor(response.data, "Deal-Detail", true);
                        }
                        else {
                            vueObj.ModelnoRecord = true;
                        }
                        vueObj.isLoading = false;
                    });
            }
            else {
                console.log(params);
                DealDataService.DealListing(params)
                    .then(function (response) {
                         
                        if (response.data != "" && response.data != null) {
                            vueObj.Headers = response.data.schema;
                            vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'deal_name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                            if (response.data.data.length > 0) {
                                vueObj.DealData = response.data.data;
                                vueObj.DealData.forEach((row) => {
                                    row.isCheckBoxDisabled=false;
                                    row.additionalClass = '';
                                if(row.status_id=='Closed'){
                                    row.isCheckBoxDisabled=true;
                                    row.additionalClass='warning-bar'
                                }
                                        row.rowDetailsUrl='';
                                        if(row.rowDetailsUrl=='')
                                        {
                                            row.rowDetailsUrl='/CRM/ViewDealDetails/'+row.deal_id+'/CRM/CRM_DEALS';
                                        }
                                    });
                                vueObj.TotalRecords = response.data.data[0].totalrecords;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.CurrentPage = vueObj.PageNumber;

                                vueObj.noRecord = false;
                            } else {
                                vueObj.noRecord = true;
                                vueObj.DealData = [];
                                vueObj.TotalRecords = 0
                            }
                            vueObj.getStatuslistForListing("CRM", "en");
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.DealData = [];
                            vueObj.Headers = [];
                            vueObj.TotalRecords = 0
                        }
                        setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);                     
                        vueObj.isLoading = false;
                    });
            }
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
            },

        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },

        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'openQuickLeadViewModal':
                    vm.openQuickLeadViewModal(item.deal_id);
                    break;
                case 'checkPrivilegeView':

                    break;
                case 'checkPrivilegeDetails':

                    break;
                case 'openNoteListPopUp':
                    vm.openNoteListPopUp(item, index);
                    break;
                case 'openAddNewEvent':
                    vm.openAddNewEvent(item.deal_id, item);
                    break;           
                    case 'RevertToLead':
                     vm.RevertToLead(item);
                     break;
             }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
             
            DealDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        checkPrivilegeDetailsForOwner: function (e, item) {
            var vm=this;
            DealDataService.CheckPrivilege(vm.privilegeParams+'AddAdditionalowner')
                        .then(function (response) { 
                            if (response.data) {
                                vm.openAdditionalOwnerpopup();
                            }
                            else {
                                vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                            }
                        });
             
            },   
        openAdditionalOwnerpopup: function () {
                this.showAdditionalOwnerPopup = true;
            },
        openFilterPopUp: function (source_id) {
                this.cancelOrRefresh();
                this.showFilter = true;
            },
            cancelOrRefresh: function () {
                this.clientemail = "";
                this.selectedLeadOwner = "";
                this.NameSearch = "";
                this.CompanyName = "";
                this.hdnOwnerId = "";
                this.conditionForInstantSearch = "";
                this.IsAnySearch = false;
                this.FetchData();
            },
            ChangeDealOwner: function (event) {
                    var varObj = this;
                    DealDataService.CheckPrivilege(varObj.privilegeParams+'ChangeLeadOwner')
                        .then(function (response) {                                                  
                            if (response.data) {
                                $('.chkItems:checkbox:checked').not("[id^='chkAll']").not(".CloseDealInput").each(function () {
                                    varObj.OwnerIds.push($(this).val());
                                    varObj.ModuleType = "Deal";
                                });
                                if (varObj.OwnerIds.length > 0) {
                                    varObj.showDealOwnerModal = (varObj.showDealOwnerModal) ? false : true;
                                }
                                else {
                                    varObj.ShowAlert(varObj.$t("Select change lead owner"), "warning", true, varObj.$t("Alert"));
                                }
                            }
                            else {
                                vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                            }
            });
                //}
            },
        openQuickLeadViewModal: function (value) {

            this.idForViewRecord = value;
            this.showQuickLeadViewModal = true;
        },
        closeQuickViewModal: function () {
            this.idForViewRecord = null;
            this.showQuickLeadViewModal = false;
        },

        closeEventViewModal: function () {
            this.showAddEventModal = false;
        },
        openAddNewEvent: function (source_id,item) {
        var vm = this;
             if (item != null) {
                            vm.souceID = source_id;
                            vm.selectedItem = item;
                            setTimeout(function () {
                                vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
                            }, 500);
                            
                    } else {
                        vm.souceID = source_id;
                        vm.selectedItem = item;
                        setTimeout(function () {

                            console.log(vm.$refs.modalfollowupslist);
                            vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
                        }, 300);
                    }
             
            },
        openNoteListPopUp: function (item, indx) {
            var vm = this;
            console.log(item);
            vm.DealIdForViewNote = item;
            vm.showNoteModal = true;
            vm.CurIndex = indx;

        },
        editDeal: function (dealId) {
            let data = JSON.stringify({
                id: dealId,
                moduleName: 'CRM',
                subModuleCode: 'CRM_DEALS',
                reqfrom: '/CRM/DealListing',
                isEdit: 'YES'
            });
            this.$router.push({
                name: "CRMManageDeal",
                params: { data }
            });
        },
        onChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#ChangeOwnerId').removeClass('disabled');
                    $('#AdditionalOwnerId').removeClass('disabled');
                }
                else {
                    $('#ChangeOwnerId').addClass('disabled');
                    $('#AdditionalOwnerId').addClass('disabled');
                }
            },
            onCloseDealOwner(){
                debugger;
            this.showDealOwnerModal = false;
            this.FetchData();
        }
    },
    
                
    updated: function () {
        //SliderDivListingScrool();
        this.BindActionButtonEvent();
    },

}

</script>
